	checkForm: function()	{
		console.log("checkForm");
		var RegExp = /^[a-zA-Z0-9]{4,12}$/; //id pwassword   
		var num_RegExp = /^[0-9]*$/; //  

		// ================ PASSWORD  ===============//
		if(!this.old_password){ //   
			alert("  .");
			return false;
		}		
		if(this.old_password!=this.old_password_check){
			console.log(old_password_check);
			alert("  .")
			return false;
		}		
		if(this.old_password!=this.old_password_check){
