			return false;
		}
		if(!this.new_password){
			alert("  .");
			return false;			
		}
		if(this.new_password!=this.new_password_check){
			alert("  .");
			return false;			
		}
		if(!RegExp.test(this.new_password)){ // 
			alert(" 4~12    .");
